import DIcon from "icon/icon";
import { TFiledType } from "store/models/forms";

const transcript = {
  fieldType: {
    keyToText: (fieldType: TFiledType) => {
      switch (fieldType) {
        case "dropdown":
          return " ";
        case "one_of_list":
          return "  ";
        case "several_of_list":
          return "  ";
        case "text_number":
          return " ()";
        case "text_float":
          return " ( c  )";
        case "text_string":
          return " ()";
        default:
          return "";
      }
    },
    keyToIconname: (fieldType: TFiledType): DIcon.name => {
      switch (fieldType) {
        case "dropdown":
          return "dropdown";
        case "one_of_list":
          return "radioButtonChecked";
        case "several_of_list":
          return "checkBox";
        case "text_float":
          return "shortText";
        case "text_number":
          return "shortText";
        case "text_string":
          return "shortText";
      }
    },
  },
};

export default transcript;
